import './TipContainer.scss'

import React from 'react'

const TipContainer = ({
  children,
  tip,
  tipVisible = true,
}: {
  children?: ReactNode
  tip?: ReactNode
  tipVisible?: boolean
}) => (
  <div className='TipContainer'>
    <div className='TipContainerchildren'>{children}</div>
    {tipVisible ? (
      <span className='TipContainerbubble'>
        <span className='TipContainerbubbleContent'>{tip}</span>
      </span>
    ) : null}
  </div>
)

export default TipContainer
